import React, { Component } from "react";
import emailjs from 'emailjs-com';
import "./ContactForm.css";
import 'react-toastify/dist/ReactToastify.min.css';
// import "semantic-ui-css/semantic.min.css";
//import { Form, Input, TextArea, Button } from 'semantic-ui-react';
import Swal from 'sweetalert2';

const SERVICE_ID = "service_hozpzec";
const TEMPLATE_ID = "template_khpz3aj";
const USER_ID = "5zySFDBjf4PmURmVk";

const ContactForm = () => {

  const handleOnSubmit = (e) => {
    e.preventDefault();
    console.log(e.target);
    emailjs.sendForm(SERVICE_ID, TEMPLATE_ID, e.target, USER_ID)
      .then((result) => {
        console.log(result.text);
        Swal.fire({
          icon: 'success',
          title: 'Message Sent Successfully'
        })
      }, (error) => {
        console.log(error.text);
        Swal.fire({
          icon: 'error',
          title: 'Ooops, something went wrong',
          text: error.text,
        })
      });
    e.target.reset()
  };

  return (
    <div className="ContactForm">
      
      <form id="contactForm" onSubmit={handleOnSubmit}>
        <label for="fname">Name</label>
        <input required type="text" id="name" name="from_name" placeholder="Your name.." />

        <label for="lname">Email</label>
        <input required type="email" id="email" name="from_email" placeholder="Your email.."/>

        <label for="message">Message</label>
        <textarea required id="message" name="message" placeholder="Write something.."></textarea>

        <input type="submit" value="Submit" />
      </form>
    </div>
  );
};

export default ContactForm;